            <div @click="evaluatea" style="display: flex; flex-direction: row;align-items: center" >
                <img src="../assets/imgs/visible.png" v-if="eval_pj === true"/>
               <img src="../assets/imgs/invisible.png" v-if="eval_pj === false">
