            ],
        }
    },
    methods:{
        evaluatea:function(){
            this.eval_pj = !this.eval_pj
        },
        state:function(obj){
            let x = this.star_list;
            if(obj.flag == true){
                for(let i = 0; i<x.length; i++) {
                    let y = x[i];
                    if (i >= obj.id) {
                        y.flag = false;
                    }
                    else{
                        y.flag = true;
                    }
                }
            }else{
                for(let i = 0; i <x.length; i ++){
                    let y = x[i];
                    if (i <= obj.id){
                        y.flag = true;
                    }else{
                        y.flag = false;
                    }
                }
            }

            if(obj.flag){
                if(obj.id == 0) {
                    this.zhuantai = '()';// 1
                }else if(obj.id == 1) {
                    this.zhuantai = '()';// 2
                }else if(obj.id == 2) {
                    this.zhuantai = '()';// 3
                }else if(obj.id == 3) {
                    this.zhuantai = '()';// 4
                }else if(obj.id == 4) {
                    this.zhuantai = '()';// 5
                }
            } else{
                if(obj.id == 0) {
                    this.zhuantai = ' ';// 0
                } else if(obj.id == 1) {
                    this.zhuantai = '()';// 1
                } else if(obj.id == 2) {
                    this.zhuantai = '()';// 2
                } else if(obj.id == 3) {
                    this.zhuantai = '()';// 3
                } else if(obj.id == 4) {
                    this.zhuantai = '()';// 4
                }
            }
            
        },
        statea:function(obj){
            let x = this.star_lista;
            if(obj.flag == true){
                for(let i = 0; i<x.length; i++) {
                    let y = x[i];
                    if (i >= obj.id) {
                        y.flag = false;
                    }
                    else{
                        y.flag = true;
                    }
                }
            }else{
                for(let i = 0; i <x.length; i ++){
                    let y = x[i];
                    if (i <= obj.id){
                        y.flag = true;
                    }else{
                        y.flag = false;
                    }
                }
            }

            if(obj.flag){
                if(obj.id == 0) {
                    this.zhuantaia = '()';// 1
                }else if(obj.id == 1) {
                    this.zhuantaia = '()';// 2
                }else if(obj.id == 2) {
                    this.zhuantaia = '()';// 3
                }else if(obj.id == 3) {
                    this.zhuantaia = '()';// 4
                }else if(obj.id == 4) {
                    this.zhuantaia = '()';// 5
                }
            } else{
                if(obj.id == 0) {
                    this.zhuantaia = ' ';// 0
                } else if(obj.id == 1) {
                    this.zhuantaia = '()';// 1
                } else if(obj.id == 2) {
                    this.zhuantaia = '()';// 2
                } else if(obj.id == 3) {
                    this.zhuantaia = '()';// 3
                } else if(obj.id == 4) {
                    this.zhuantaia = '()';// 4
                }
            }
            
        },

    }
}
</script>